import {export_json_to_excel} from '../assets/excel/Export2Excel';

export function export2Excel(exportList, title) {
const tHeader =['', '','','','','', ''
    , '', '','',''];

    const filterVal =
        ['matde', 'number','measuStr','po', 'provider','typeStr',
            'proName', 'StatusStr','remark', 'createTime','updateTime'];

    const data = formatJson(filterVal, exportList);

    title = title || '';
    export_json_to_excel(tHeader, data, title);

}

function formatJson(filterVal, jsonData) {
    return jsonData.map(v => filterVal.map(j => v[j]))
}



