<template>
    <div class="app-container">
        <div class="grid-content">{{title}}</div>

        <!---->
        <el-form :model="queryParams" ref="queryForm" :inline="true">
            <el-form-item label="" prop="matde">
                <el-input
                    v-model="queryParams.matde"
                    placeholder=""
                    clearable
                    style="width: 240px"
                    @keyup.enter.native="handleQuery"
                />
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select v-model="typeVal" multiple clearable placeholder="" style="width: 240px">
                    <el-option
                        v-for="item in moveType"
                        :key="item.index"
                        :label="item.title"
                        :value="item.index">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="proId">
                <el-select v-model="queryParams.proId" filterable clearable placeholder="" style="width: 240px">
                    <el-option
                        v-for="item in projectData"
                        :key="item.proId"
                        :label="item.name"
                        :value="item.proId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    v-model="dateRange"
                    size="small"
                    style="width: 240px"
                    value-format="yyyy-MM-dd"
                    type="daterange"
                    range-separator="-"
                    start-placeholder=""
                    end-placeholder=""
                ></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"></el-button>
            </el-form-item>
        </el-form>

        <!---->
        <el-row :gutter="10" class="handle">
            <el-col :span="1.5">
                <el-button
                    type="primary"
                    icon="el-icon-download"
                    size="mini"
                    @click="exportClick"
                ></el-button>
            </el-col>
        </el-row>

        <!---->
        <el-table
            :data="tableData"
            border
            :row-style="isStatus"
            v-loading="loading"
            style="width: 100%">
            <el-table-column type="index" width="50" />
            <!--<el-table-column prop="matnr" label="" />-->

            <el-table-column prop="matde" label="" />

            <el-table-column prop="number" label="" />

            <el-table-column prop="measuStr" label="" />

            <el-table-column prop="po" label="" />

            <el-table-column prop="provider" label="" />

            <el-table-column prop="typeStr" label="" />

            <el-table-column prop="proName" label="" />

            <el-table-column prop="StatusStr" label="" />

            <el-table-column prop="remark" label="" />

            <el-table-column prop="createTime" label="" width="150"/>

            <el-table-column prop="updateTime" label="" width="150"/>
        </el-table>

        <!---->
        <el-pagination
            background
            layout="prev, pager, next"
            :current-page="queryParams.pageNum"
            :page-count="pageCount"
            @current-change="currentChange"
        >
        </el-pagination>

    </div>
</template>

<script>
    import {invoicing} from "../../utils/invoicing";
    import {export2Excel} from "../../utils/export2Excel";
    import {
        getInvoicingList,
        getProjectList,
        getMaterialsList,
        getProviderList,
    } from "../../utils/actions";
    import {addDateRange, addMoveType} from "../../utils/common";

    export default {
        data() {
            return {
                title: '',
                loading: true,
                //
                pageCount: 1,
                //
                tableData: [],
                //
                unitGroup: [],
                //
                moveType: [],
                //query
                dateRange: [],
                //query
                typeVal: [],
                //
                projectData: [],
                // 
                queryParams: {},
                //
                materialsList: [],
                //
                providerList: [],
            }
        },
        methods: {
            /**  */
            getList() {
                this.loading = true;
                //console.log(this.queryParams);
                if(!this.queryParams.type) {
                    this.queryParams.type = "'A1','A2','A3','A4','A5','B1','B2','C1','C2','D1','D2'";
                }
                getInvoicingList(this.queryParams).then(response => {
                    this.tableData = response.data;

                    //
                    this.selectDictLabel(this.tableData, 'measu', this.unitGroup);
                    this.selectDictLabel(this.tableData, 'type', this.moveType);
                    this.selectDictLabel(this.tableData, 'Status', invoicing.statusType);

                    this.pageCount = response.page_num;
                    this.loading = false;
                });

            },
            /**  */
            handleQuery() {
                this.queryParams.pageNum = 1;
                addDateRange(this.queryParams, this.dateRange);
                addMoveType(this.queryParams, this.typeVal);
                this.getList();
            },
            /**  */
            resetQuery() {
                this.dateRange = [];
                this.typeVal = [];
                this.$refs.queryForm.resetFields();
                this.handleQuery();
            },

            currentChange(pageNum) {
                this.queryParams.pageNum = pageNum;
                this.getList();
            },
            //
            isStatus({row, column, rowIndex, columnIndex}) {
                if (row.Status === "N") {
                    return {
                        backgroundColor: '#f9bdbd'
                    }
                }
            },

            //
            exportClick(){
                addDateRange(this.queryParams, this.dateRange);
                addMoveType(this.queryParams, this.typeVal);
                if(!this.queryParams.type) {
                    this.queryParams.type = "'A1','A2','A3','A4','A5','B1','B2','C1','C2','D1','D2'";
                }
                this.queryParams.pageNum = undefined;
                this.queryParams.pageSize = undefined;
                getInvoicingList(this.queryParams).then(response => {
                    const data = response.data;
                    //
                    this.selectDictLabel(data, 'measu', this.unitGroup);
                    this.selectDictLabel(data, 'type', this.moveType);
                    this.selectDictLabel(data, 'Status', invoicing.statusType);
                    //
                    export2Excel(data, ''+ this.getFormatDate('MMDDhhmmss'));
                })

            },
        },
        created() {
            this.unitGroup = invoicing.unitGroup;
            this.moveType = invoicing.moveType;
            this.queryParams = invoicing.queryParams();

            this.getList();
            getProjectList().then(response => {
                this.projectData = response.data;
            });
            getMaterialsList().then(response => {
                this.materialsList = response.data;
            });
            getProviderList().then(response => {
                this.providerList = response.data;
            });
        }
    }
</script>

<style scoped lang="scss">
    .handle {
        margin-bottom: 20px;
    }
    .el-pagination {
        margin: 20px 0;
    }
    .el-icon-delete {
        margin: 4px 20px;
        font-size: 24px;
    }

</style>



